import React, { useState } from 'react'
import { Typography, Card, CardContent, CardMedia } from '@mui/material'
import Carousel from 'react-material-ui-carousel'
import { Waypoint } from 'react-waypoint'

import './testimonials.css'


function Item(props)
{
  return (
    <Waypoint 
      onLeave={props.setPlaying(false)}
    >
    <div style={{ height: "100%", display: "flex", flexDirection: "column", gap: "4rem" }}>
      {props.item.map(item => {
        return (
          <Card className="testimonial">
            <CardContent>
              {item.video ? <CardMedia component="video" controls src={item.video} playing={props.playing} style={{ paddingBottom: "2rem" }} /> : ""}
              {item.comments ? <Typography variant="h6" className="comments">{item.comments}</Typography> : ""}
              <div style={{ display: "flex", alignItems: "center", justifyContent: "center", gap: "1rem" }}>
                {item.img ? <img src={item.img} alt={item.user} /> : ""}
                <Typography variant="h6" className="user-name">{item.user}</Typography>
              </div>
            </CardContent>
          </Card>
        )
      })}
    </div>
    </Waypoint>
  )
}

const items = [
  [
    {
      user: "Chhavi, USA",
      comments: "I wanted yoga to help me in connecting with myself, to be more present in reality. Now when I look back before I joined you, I see so much changes in me now. Yes the body has changed tremendously, I feel so balanced and calm within my whole body. My body has become so light. But with all that, my mind, it has completely shifted. I feel connected with myself, more importantly I am more accepting of myself now.",
      img: "",
      video: ""
    },
    {
      user: "Ashley, Toronto",
      comments: "I started Maitreyee’s morning yoga class after struggling for almost a year with pain in my back, neck, shoulder, arm, all the way to my wrist. Through morning meditation and yoga, I  learned how to relax my body and mind. Learning to be present and my connection to my breath helped me navigate my world physically with more ease.",
      img: "images/ashley.jpeg",
      video: ""
    }
  ],
  [{
    user: "Sindhujan Vidurshika, Toronto",
    comments: "",
    img: "images/sindhujan.jpeg",
    video: "video/foot drop.mp4"
  }],
  [{
    user: "Keireen, Toronto",
    comments: "",
    img: "",
    video: "video/keireen canada.mp4"
  }]
]

const Testimonials = () => {
  
  const [playing, setPlaying] = useState();
  
  return (
    <section id="testimonials">
      <Typography variant="h3" className="title">
        Client Reviews
      </Typography>
      <Carousel indicators={false} navButtonsAlwaysVisible={true} autoPlay={false} className="testimonials-carousel">
        {
          items.map( (item, i) => <Item key={i} item={item} playing={playing} setPlaying={setPlaying} /> )
        }
      </Carousel>
    </section>
  )
}

export default Testimonials